@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono:ital,wght@0,100..700;1,100..700&display=swap');

$bg-main-color: #00171f;
$vermelho: #fa4454;

@mixin dimensoes($altura,$largura){
    height: $altura;
    width: $largura;
}

@mixin container($altura: 100vh, $largura: 100vw, $espacamento-interno: 80px){
    width: $largura;
    height: $altura;
    padding: $espacamento-interno;
}

@mixin flex-box ($display, $justificacao, $alinhamento) {
    display: $display;
    justify-content: $justificacao;
    align-items: $alinhamento;
}

@mixin bg-images($url,$size: contain,$repeat: no-repeat){
    background-image: url("#{$url}");
    background-repeat: no-repeat;
    background-size: contain;
}

*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    text-decoration: none;
    color: white;
    font-family: "Roboto Mono", monospace;
}

main{
    @include container;
    @include flex-box(flex, space-between, center);
    background-color: $bg-main-color;

    .conteudo-text{
        width: 500px;

        h1{
            font-size: 70px;
        }

        p{
            font-size: 25px;
            margin-top: 3rem;
            
        }
    }

    .personagem{
        img{
            width: 450px;
            @include bg-images("../src/img/controlador.png");
        }
    }

    .habilidades{
        max-height: 600px;
        @include flex-box(column, center, center);
        background-color: $vermelho;

        .cards-habilidades, img{
            @include dimensoes(100px, 100px);
        }
        
        .cards-habilidades{
            img{
                filter: opacity(70%);
                &:hover{
                    transition: all 0.2s ease-out;
                    transform: scale(1.2);
                    filter: opacity(100%);
                }
            }
        }
    }
}

footer{
     /* @include dimensoes(100px, 100vw); */
     @include container(100px, 100vw, 20px);
     @include flex-box(flex, auto, center);
    background-color: red;


     .prev-caracters{
        order: 1;
         @include dimensoes(80px, 60px);
         @include bg-images("../src/img/prev-btn.png");
     }
 }

@media (max-width: 600px){
    main{

        height: auto;
        
        flex-direction: column;
        gap: 50px;

        .conteudo-text{
         width: 300px;
         order: 2;

            h1{
                font-size: 50px;
                text-align: center;
            }

            h2{
                font-size: 15px;
                text-align: center;
            }

            p{
                font-size: 20px;
                margin-top: 1rem;
                text-align: center;
            }
        }

        .personagem{
            order: 1;
            img{
                width: 300px;
                background-size: 300px;
            }
        }

        .habilidades{
            order: 3;
            
            @include flex-box(flex, center, center);
    
            .cards-habilidades, img{
                @include dimensoes(80px, 80px);
            }
        }
    }
}